.feed {
    flex: 0.4;
    border-right: 1px solid var(--x-color);
    min-width: fit-content;
    overflow-y: scroll;
}

.feed::-webkit-scrollbar {
    display: none;
}

.feed {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.feed__header {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--x-color);
    padding: 15px 20px;
}

.feed__header > h2 {
    font-size: 20px;
    font-weight: 800;
    text-align: center;
}

@media (max-width: 768px) {
    .feed {
      flex: 1;
      min-width: 0; 
      max-width: 100%; 
    }
  }