.post{
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--x-color);
    padding-bottom: 10px;
}

.post__body {
    flex: 0.5;
    padding: 10px;
}

.post__body > img {
    border-radius: 20px;
    width: 100%;
}

.post__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post__badge {
    font-size: 14px !important;
    color: var(--x-color);
}

.post__headerSpecial {
    font-weight: 600;
    font-size: 12px;
    color: grey;
}

.post__avatar {
    padding: 20px;
}